@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
body {
  padding: 0;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #f8f8f8;
}

.App {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Login {
  background-color: white;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.1);
}

.Login .title{
  font-weight: bold;
  margin-bottom: 12px;
}

.Login .submit-button{
  margin-top: 12px;
}
.Signup{
  background-color: white;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.1);
}

.Signup .title {
  font-weight: bold;
  margin-bottom: 12px;
}

.Signup .submit-button {
  margin-top: 12px;
}

.Home {
  text-align: center;
}

.Home h2 {
  font-weight: bold;
  margin-bottom: 10px;
}

.Home span {
  font-weight: 300;
}
.Home h6 {
  margin-top: 10px;
}
.Home .buttons {
  margin-top: 10px;
  display: flex;
  justify-content: space-around;
}

.Home .buttons button {
  padding: 0;
}

.Home a {
  text-decoration: none;
  color: white;
  padding: 10px 30px;
}
